import React from 'react'
 import "./services.css"
 import zoomcar from '../../img/zoomCar.jpeg'
 import zara from '../../img/zara.com.jpeg';
 import { themeContext } from '../../Context';
import { useContext } from 'react';
import Cardses from "../cards/cards"
import resume from "./Mitali-Sinha-Resume.pdf"
import {motion} from 'framer-motion'

const Servicess = () => {
    let transition= {duration:3.5, type:"spring"}
    let theme = useContext(themeContext);
    let darkMode = theme.state.darkMode;
  return (
    <>
    <hr id="project"/>
    <div className="servicess" >
        {/* left side */}
        <div className="awesome">
           <span style={{color:darkMode?"white":''}}>My Awesome</span>
           <span>Projects</span>
           <a href={resume} download>
           <div className="button s-button">Download CV</div>
           </a>
           <div className="blur s-blur" style={{background:darkMode?"black":"#ABF1FF94",}}></div>
        </div>
        {/*right side  */}
        <div className="cardses">
            <motion.div style={{left:"42rem",top:"-4rem"}}
            whileInView={{left:"33rem"}}
            initial={{left:"42rem"}}
            transition={transition}
            >
                <Cardses src={zoomcar} heading="ZoomCar.com" details="An online car booking website.S" href1="https://magical-kashata-a20ef2.netlify.app/" href2="https://github.com/zuhedshaikh95/Zoomcar-Clone"/>
            </motion.div>
            <motion.div style={{left:"7rem", top:"6rem"}}
             whileInView={{left:"15rem"}}
             initial={{left:"7rem"}}
             transition={transition}
            >
                <Cardses src={zara} heading="zara.com" details="zara.com is an  online shopping commercial website." href1="https://earnest-pixie-22cf63.netlify.app//" href2="https://github.com/mira713/zara.com/tree/main/zara.com/public-bead-6343"/>
            </motion.div>
            <motion.div style={{left:"36rem", top:"17rem"}}
            whileInView={{left:"30rem"}}
            initial={{left:"36rem"}}
            transition={transition}
            >
                <Cardses src="" heading="" details=""/>
            </motion.div>
            <div className="blur s-blur" style={{background:darkMode?"black":"var(--purple)"}}></div>
        </div>
    </div>
    </>
  )
}

export default Servicess