import React from 'react'
import './intro.css'
import Github from '../../img/github.png'
import Linkedin from '../../img/linkedin.png'
import thumbup from '../../img/thumbup.png'
import crown from '../../img/crown.png';
import { useContext } from 'react'
import { themeContext } from '../../Context'
import myPic from '../../img/myPic.jpeg'
import FloatingDiv from '../floatingDiv/FloatingDiv'
import { motion } from 'framer-motion'


const Intro = () => {
   let transition= {duration:2, type:"spring"}
    let theme = useContext(themeContext)
    let darkMode = theme.state.darkMode

  return (
    <>
    <div className="intro" id="Navbar">
        <div className="i-left">
            <div className="i-name">
                <span style={{color:darkMode?"white":''}}>Hi! I am</span>
                <span>Mitali Sinha</span>
                <span  style={{color:darkMode?"white":''}}> A Web Developer, skilled in HTML, CSS , javaScript,
                    react, redux, producing a good quality of web applications.
                </span>
            </div>
            <button className='button i-button'>Hire Me</button>
            <div className="i-icon">
                <div>
                <a href=''>
                <img src={Github} alt="github" color="white"/>
                </a>
                </div>
                <div >
                <a href=''>
                <img src={Linkedin} alt="Linkedin" background="var(--orange)"/>
                </a>
                </div>
            </div>
        </div>
        <div className="i-right">
            {/* <img src={vector1} alt="github"/>
           <img src={vector2} alt="github"/> */}
           <div></div>
           <div></div>
           <div >
            <img src={myPic} alt='me' style={{height:"100%", width:"100%", borderRadius:"100%", marginLeft:"-10%"}}/>
            </div>
            {/* <img src={glass} alt="imoji"/> */}
            <motion.img src="https://st2.depositphotos.com/1007566/7897/v/950/depositphotos_78971892-stock-illustration-cartoon-emoticons-design.jpg"
            initial={{top:"-100%", left : "-84%"}}
            whileInView={{left:"-70%"}}
            transition={transition}
            />
            
            <motion.div style={{top:'-3%', left:"60%", color:darkMode?"black": "", }} 
            initial={{top:"-3%",left:"74%"}}
            whileInView={{left:"55%"}}
            transition={transition}
            >
            <FloatingDiv image={crown} txt1="Web" txt2="Developer"/>
             </motion.div>

             <motion.div style={{top:"80%", left: "-14%" ,color: darkMode?"black" :'', top:"80%",left:"15%"}}
             initial={{top:"80%", left:"-14%"}}
             whileInView={{left:"0%"}}
             transition={transition}
             className="float"
             >
            <FloatingDiv image={thumbup} txt1="best quality" txt2="websites"/>
             </motion.div>

        {/* blur divs */}
             <div className='blur' style={{background:darkMode?"black":"rgb(238 210 255)"}}></div>
             <div className="blur" style={{background:darkMode?"black":'rgb(169, 187, 252)',top : "35rem", width:"21rem", height:"11rem", left:"20rem"}}></div>
        </div>
    </div>
    </>
  )
}

export default Intro