import React from 'react'
import { themeContext } from '../../Context';
import { useContext } from 'react';
import './work.css'
import {motion} from 'framer-motion'

const Work = () => {

    let theme = useContext(themeContext);
    let darkMode = theme.state.darkMode;
 let html="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQf49M65IceT_WUdCIe1uOX1rlLRHapJPfsGA&usqp=CAU"
 let css="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQfSSFChgWCIFNSPsb80d3R4O9bRbO49vg1joHOU9MPehcR7UkzSQ2gXFxm1BpkUXGiYrQ&usqp=CAU"
 let js='https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTeksMoIpBDYBQefxGvPy_qQ4a0EQQRGio6XQxsbVLK7JZlLhYxTAn_&usqp=CAU'
 let redux='https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR3faEchkZHKg0p7Z9THuRbpI2DJFnW0k1_9g&usqp=CAU'
 let react='https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQ5N9w0VkLEi3ywCilE_tH-cZv6ITa0mvmbxNITUmis__1pPaI47Jam&usqp=CAU'
    
  return (
    <div>
    <hr id="skill"/>
    <div className="works">
        <div className="awesome">
            <span style={{color:darkMode? "white": 'black'}}>Interested in</span>
            <span>these tools</span>
            <span style={{color:darkMode? "white": 'black'}}>I am skilled and interested in tools like 
                <br/>
                HTML, CSS, JavaScript, React, Redux, Node.js
            </span>
            <br/>
            <div className="button">Hire Me</div>
            {/* <div className='blur s-blur' style={{background:"ABF1FF94"}}></div> */}
        </div>
        <div className="w-right">
          <motion.div 
          initial={{rotate:90}}
          whileInView={{rotate:0}}
           viewport={{margin:"-40px"}}
          transition={{duration:7, type:'spring'}}
          className="w-maincircle">
                <div className="w-secCircle">
                    <img src={html} alt="html" />
                </div>
                <div className="w-secCircle">
                    <img src={css} alt="css"/>
                </div>
                <div className="w-secCircle">
                    <img src={js} alt="js"/>
                </div>
                <div className="w-secCircle">
                    <img src={react} alt="react"/>
                </div>
                <div className="w-secCircle">
                    <img src={redux} alt="redux"/>
                </div>
          </motion.div>
          {/* backgroundCircle */}
          <div className="b-circle blueCircle"></div>
          <div className="b-circle pinkCircle"></div>
          <div className="blur w-blur" style={{background:darkMode?"black":"var(--purple)"}}></div>
          <div className="blur" style={{background:darkMode?"black":'rgb(169, 187, 252)',top : "25rem", width:"21rem", height:"11rem", left:"25rem"}}></div>
        </div>
     </div>
    </div>
  )
}

export default Work