import React from 'react'
import './navbar.css'
import Toggle from "../Toggle/Toggle"
import { themeContext } from '../../Context';
import { useContext } from 'react';
import {Link} from 'react-scroll'

const Navbar = () => {
    let theme = useContext(themeContext);
    let darkMode = theme.state.darkMode;
  return (
    <>
    <div className='n-wrapper' style={{backgroundColor:darkMode?'black' : ''}}>
        <div className='n-left'>
            <div className='n-name'>Mitali</div>
            <Toggle/>
        </div>
        <div className='n-right'>
            <div className='n-list'>
                <ul style={{listStyleType:"none"}}>
                    <Link spy={true} to="Navbar" smooth={true} activeClass="activeClass">
                    <li>Home</li>
                    </Link>
                    <Link spy={true} to="aboutMe" smooth={true}>
                    <li>AboutMe</li>
                    </Link>
                    <Link spy={true} to="skill" smooth={true}>
                    <li>skills</li>
                    </Link>
                    <Link spy={true} to="project" smooth={true}>
                    <li>Projects</li>
                    </Link>
                </ul>
            </div>
            <Link spy={true} to="contact" smooth={true}>
            <button className='button n-button'>Contact</button>
            </Link>
        </div>
    </div>
    </>
  )
}

export default Navbar