import React from 'react'
import './about.css'
import {Swiper, SwiperSlide} from 'swiper/react'
import { Pagination } from 'swiper';
import "swiper/css/pagination"
import 'swiper/css';
import { useContext } from 'react'
import { themeContext } from '../../Context'


const About = () => {
  let theme = useContext(themeContext)
  let darkMode = theme.state.darkMode

    let degree='https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQXcISzhjVtsDqvhSKzo0-lix_ekktGnCYBCg&usqp=CAU';
    let masai = 'https://news.easyshiksha.com/wp-content/uploads/Masai-324x160.jpg'
    let school ='https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQn6wbjdFR8CGzVGb1z4Ekt1PANsQvgUiWAjw&usqp=CAU'
  
    let mySelf=[
      {
        img:masai,
        review : "Currently honeing my skills in Masai, A skill building School. Learing Skills like HTML, CSS, JavaScript, React, redux and many more.Looking foword to use my skills and knowledge in a growth oriented organisation." 
      },
      {
        img: degree,
        review : "Completed my Graduation in Commerce from VBU, Jharkhand and took training in management and soft skill from Institute of Skills , run by GOI and led by IL&FS company."
      },
      {img : school,
       review : "Completed Schools from Pawan Krus School, Ramgarh, Jharkhand and passed 12th with distinction in economy, got first rank in Ramgarh district."
      }
    ]
  
    return (
      <>
      <hr  id="aboutMe"/>
    <div className="a-wrapper">
      <div className="a-heading">
        <span>My</span>
        <span> Educational</span>
        <span> Qualification...</span>
      </div>
      {/* Swiper / Slide */}
     <Swiper
     modules={[Pagination]}
     slidesPerView={1}
     pagination={{clickable:true}}
     >
     {mySelf.map((self,index)=>{
      return <SwiperSlide key={index}>
        <div className="mySelf">
        <img src={self.img}alt=""/>
        <span>{self.review}</span>
        </div>
      </SwiperSlide>
     })}
     </Swiper>
     <div className="blur a-blur" style={{background:darkMode?"black":"var(--purple)"}}></div>
    </div>
    </>
  )
}

export default About