import React,{useRef} from 'react'
import './contact.css'
import emailjs from '@emailjs/browser';
import { useContext } from 'react'
import { themeContext } from '../../Context'

const Contact = () => {
    const form = useRef();
    let theme = useContext(themeContext)
    let darkMode = theme.state.darkMode

    const sendEmail = (e) => {
      e.preventDefault();
  
      emailjs.sendForm('service_n4ys4b3', 'template_04wzh47', form.current, 'hsInv32YWaPjiMsSl')
        .then((result) => {
            console.log(result.text);
            alert('thanks for contacting me!')
        }, (error) => {
            console.log(error.text);
        });
    };

  return (
    <>
    <hr id="contact"/>
    <div className="contact-form">
        <div className="c-left">
            <div className="awesome"style={{marginTop:"2rem"}}>
                <span style={{color:darkMode? "white" : "black"}}>Get In Touch...</span>
                <span>Contact Me</span>
            </div>
        </div>
        <div className="c-right">
            <form onSubmit={sendEmail} ref={form}>
                <input className="user" type="text" placeholder="Name" name="user_name"/>
                <input className="user" type="email" placeholder="Email" name="user_email"/>
                <textarea className="user" placeholder="Message" name="message"/>
                <input className="button" value="send" type="submit" style={{marginLeft:"10rem"}}/>
            </form>
        </div>
        <div className="blur cc-blur" style={{background:darkMode?"black":"var(--purple)"}}></div>
    </div>
    
    <div className='footer'></div>
    </>
  )
}

export default Contact