import React from 'react'
import "./cards.css"

const Cards = ({src,details,heading,href1,href2}) => {
  return (
    <div className="cards">
      <img src={src} alt="heading"/>
      <span style={{fontWeight:"bold"}}>{heading}</span>
      <span>{details}</span>
      <div style={{display:"flex",}}>
      <a href={href1}>
      <div className="c-button"style={{marginRight:"5px"}}>deployed</div>
      </a>
      <a href={href2}>
      <div className="c-button" style={{marginLeft:"5px"}}>hithub</div>
      </a>
      </div>
    </div>
  )
}

export default Cards